* {
  margin: 0;
  top: 0;
  left: 0;
  cursor: default;
  user-select: none;
}

:root {
  --red: rgb(250, 137, 137);
}

@font-face {
  font-family: "mulish";
  src: url("./assets/fonts/Mulish-Light.ttf") format("truetype");
}

@font-face {
  font-family: "lobster";
  src: url("./assets/fonts/Lobster-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Cormorant-it";
  src: url("./assets/fonts/CormorantGaramond-LightItalic.ttf");
}

@font-face {
  font-family: "Roboto-r";
  src: url("./assets/fonts/Roboto-Regular.ttf");
}

@font-face {
  font-family: "Alegreya";
  src: url("./assets/fonts/AlegreyaSans-MediumItalic.ttf");
}

@font-face {
  font-family: "elmis";
  src: url("./assets/fonts/ElMessiri-Regular.ttf");
}

body {
  font-family: "Cormorant", serif;
  background-color: rgb(50, 50, 50);
}

h1, h2, h3 {
  display: block;
  text-align: center;
  color:var(--red)
}

h1 {
  font-size: 70px;
  font-family: "lobster", sans-serif;
  font-weight: 400;
  border-bottom: 3px solid var(--red);
}

h2 {
  font-family: "Alegreya", sans-serif;
  font-size: 56px;
  font-weight: 400;
}

h4 {
  font-family: "lobster", sans-serif;
}

h3 {
  font-size: 40px;
  font-weight: 400;
}

li {
  list-style-type: none;
  cursor: pointer;
}

p, span {
  font-size: 20px;
  text-align: justify;
  font-family: "Roboto-r", sans-serif;
}

a {
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.intersection {
  margin: 15px 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
}


/*Шапка сайта*/
.site-header {
  position: sticky;
  top: 0px;
  width: 100%;
  z-index: 999;
  display: flex;
  flex-direction: row;
  -ms-flex-direction: row;
  justify-content: space-between;
  font-size: 22px;
  background-color: rgb(50, 50, 50);
  height: 50px;
}

.slideouted-menu {
  top: 0px;
  animation-name: slidein;
  -o-animation-name: slidein;
  -moz-animation-name: slidein;
  -webkit-animation-name: slidein;
  animation-duration: 0.5s;
  -o-animation-duration: 0.5s;
  -moz-animation-duration: 0.5s;
  -webkit-animation-duration: 0.5s;
  animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}

.sticky-menu {
  top: -50px;
  animation-name: slideout;
  -o-animation-name: slideout;
  -moz-animation-name: slideout;
  -webkit-animation-name: slideout;
  animation-duration: 0.5s;
  -o-animation-duration: 0.5s;
  -moz-animation-duration: 0.5s;
  -webkit-animation-duration: 0.5s;
  animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}

@keyframes slidein {
  from {
    top: 0px;
  }
  to {
    top: -50px;
  }
}

@keyframes slideout {
  from{
    top: -50;
  }

  to {
    top: 0;
  }
}

.logo-wrapper {
  width: 50px;
  height: 50px;
  padding-left: 100px;
}

.logo-wrapper>img {
  width: 100%;
  height: 100%;
}

.menu-wrapper {
  padding-right: 100px;
  width: fit-content;
  align-items: center;
  justify-items: center;
}

.menu-wrapper>ul {
  width: 404px;
  margin: 0;
  height: 100%;
  display: flex;
  padding-left: 0;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}

.menu-wrapper>ul>a {
  border-right: 2px solid var(--red);
  height: 100%;
  min-width: 150px;
  width: fit-content;
}

.menu-wrapper>ul>a>li {
  text-align: center;
  line-height: 2;
}

.menu-wrapper>ul>a:first-child {
  border-right: 2px solid var(--red);
  border-left: 2px solid var(--red);
}

.sticked {
  position: sticky;
}

/*1-раздел*/
.greets {
  position: relative;
  z-index: 1;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  align-items: center;
  justify-content: center;
}

.text h2 {
  padding: 0 0 25px 0;
}

.simple-text {
  text-align: center;
  font-size: 40px;
  font-family: "Cormorant-it", sans-serif;
}

.about-list {
  list-style: disc;
  cursor: default;
}

.text>ul>li:last-child {
  margin-bottom: 15px;
}

ul>p {
  padding: 0;
}

/*Подробнее*/
.Rmore {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Rmore-txt {
  max-width: 1000px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  -ms-flex-direction: row;
  justify-content: center;
  text-align: center;
}

.Rmore-element {
  margin: 25px;
  min-width: 250px;
  max-width: 25%;
  color: white;
}

.icons {
  width: 120px;
  padding: 5px;
  border: 2px solid var(--red);
  border-radius: 50%;
}

.Rmore-element>a {
  cursor: pointer;
  width: fit-content;
}

.card-head {
  font-size: 26px;
  font-weight: 400;
}

.card-tetx {
  font-size: 20px;
  text-align: center;
}

/*ЦЕНЫ*/
.about-wrapper{
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.cathead {
  display: flex;
  justify-content: space-between;
}

.cathead>p {
  font-size: 25px;
  color: rgb(250, 137, 137);
}

.type-descr {
  opacity: 0;
  margin-right: 1%;
  min-width: 100%;
  width: 100%;
  transition: all 1s;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  -moz-transition: all 1s;
}

.slider-wr {
  width: 100%;
  overflow: hidden;
}

.slide {
  translate: -0%;
  display: flex;
  flex-direction: row;
  overflow: show;
  transition: translate 1s;
  -webkit-transition: translate 1s;
  -moz-transition: translate 1s;
  -o-transition: translate 1s;
}

.descr-header {
  font-size: 20px;
  font-family: "Roboto-r", sans-serif;
}

.descr-header:hover{
  cursor: pointer;
  text-decoration: underline;
}

.type-descr.show-txt{
  visibility: visible;
  opacity: 1;
}

.underline {
  text-decoration: underline;
}

.cover {
  position: absolute;
  top: -25px;
  left: -25px;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  width: 110%;
  height: 110%;
  background-image: inherit;
  filter: blur(5px);
  box-shadow: 0 0 0 6000px inset rgba(0, 0, 0, 0.7);
  overflow: hidden;
  z-index: 2;
}

.text {
  display: block;
  color: white;
  z-index: 3;
  width: 1000px;
  padding: 20px 0;
  width: 100%;
  padding: 20px 0;
  max-width: 1000px;
}

.about-wrapper {
  min-height: 100vh;
  background-color: rgb(50, 50, 50);
  position: relative;
  z-index: 1;
}

.list-elements{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
}

.list-element{
  width: fit-content;
}

.txt-wr {
  display: block;
  position: relative;
  color: white;
  width: 1000px;
  margin: auto;
  padding: 20px 0;
  z-index: 999;
}

/*Стэк*/
.Tech {
  min-height: 100vh;
  background-color: rgb(50, 50, 50);
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Tech-txt {
  max-width: 1000px;
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-tech {
  display: flex;
  flex-direction: row;
  -ms-flex-direction: row;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.icons-tech {
  height: 120px;
  align-self: center;
  cursor: pointer;
}

.Tech-element{
  margin: 25px;
  min-width: 250px;
  max-width: 25%;
  color: white;
}

.Tech-element:nth-child(2)>a>img {
  background-color: white;
  border-radius: 50%;
}

.Tech-element:nth-child(4)>a>img {
  background-color: white;
  border-radius: 50%;
}

.card-head {
  text-align: center;
}

.base-tech {
  display: flex;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  text-align: center;
}

.Tech-element-base {
  margin: 10px;
}

.icons-tech-base {
  height: 50px;
}

.Tech-element-base>a{
  height: fit-content;
  width: fit-content;
}

.card-head-base {
  color: white;
  font-size: 20px;
  font-weight: 200;
  text-align: center;
}

/*Портфолио*/
.portf-wr {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.construct {
  width: 100%;
  height: 30px;
  background-image: repeating-linear-gradient(45deg, yellow, yellow 20px, black 20px, black 40px);
}

/*Контакты*/
.contacts-wr {
  position: relative;
  z-index: 1;
  min-height: 500px;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  align-items: center;
}

.social-wr {
  display: flex;
  flex-direction: row;
  -ms-flex-direction: row;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  justify-content: center;
  margin: 25px 0;
}

.social {
  width: fit-content;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  align-items: center;
  margin-right: 25px;
}

.social>p {
  text-align: center;
  user-select: text;
}

.social-ico {
  height: 60px;
  cursor: pointer;
}

.social-wr>div>a {
  position: relative;
  width: fit-content;
  height: fit-content;
}

.form {
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  font-size: 24px;
  align-items: center;
  align-self: center;
  max-width: 300px;
  min-width: 200px;
}

.form>* {
  margin: 5px 0 0 0;
  font-size: 24px;
}

.e-mail-form {
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
}

.e-mail-form>p {
  text-align: center;
}

.link {
  cursor: pointer;
  color: rgb(250, 137, 137);
}

.sub {
  box-sizing: border-box;
  padding: 10px 20px;
  width: 150px;
  text-align: center;
  border-radius: 30px;
  display: block;
  cursor: pointer;
}

input {
  border-radius: 10px;
  border: 1px solid black;
  background-color: rgba(255,255,255,0.6);
  cursor: text;
  min-width: 260px;
  max-width: 300px;
}

.popup {
  display: inline;
  width: 145px;
  position: absolute;
  background-color: rgb(255, 246, 234);
  color: black;
  opacity: 1;
  transition-property: width, opacity;
  -o-transition-property: width, opacity;
  -moz-transition-property: width, opacity;
  -webkit-transition-property: width, opacity;
  transition-duration: 200ms;
  -o-transition-duration: 200ms;
  -moz-transition-duration: 200ms;
  -webkit-transition-duration: 200ms;
}

.popup>span {
  font-size: 16px;
  width: fit-content;
  transition: all 200ms;
  -o-transition: all 200ms;
  -moz-transition: all 200ms;
  -webkit-transition: all 200ms;
}

.hidden {
  opacity: 0;
  width: 0;
  font-size: 0;
}

.hidden>span {
  font-size: 0;
  width: 0;
}

.f-wr {
  height: 50px;
  background-color: rgb(50, 50, 50);
}

.footer {
  font-size: 20px;
  margin: auto;
  text-align: center;
  color: white;
}

/*Медиа-запросы*/
@media screen and (min-width: 280px) and (max-width: 1279px) {
  body {
    width: 100%;
  }

  h1 {
    font-size: 36px;
  }

  h2, .simple-text {
    font-size: 32px;
  }

  h3 {
    font-size: 26px;
  }

  p, h4, span {
    font-size: 16px;
  }

  .logo-wrapper,
  .menu-wrapper {
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    padding: 0;
    align-items: center;
    justify-items: center;
  }

  .txt-wr {
    padding: 20px, 0;
    width: 95%;
  }

  .text {
    padding: 20px, 0;
    width: 95%;
  }

  .menu-wrapper{
    width: 150px;
    margin: 0;
    padding: 0;
    justify-content: center;
    position: relative;
    height: 50px;
  }

  .dd-menu-cont {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 70px;
    color: transparent;
    transition: all, 0.5s;
    -o-transition: all, 0.5s;
    -moz-transition: all, 0.5s;
    -webkit-transition: all, 0.5s;
    z-index: 0;
  }

  .dd-menu-cont.show {
    opacity: 1;
    visibility: visible;
    z-index: 999;
    background-color: rgb(50, 50, 50);
    color: rgba(0,0,0,1);
    top: 50px;
  }

  .dd-menu-cont>ul>a>li {
    margin: 20px 0;
  }

  .menu-btn-wr{
    display: block;
    overflow: hidden;
    cursor: pointer;
    margin: 0;
    padding: 0;
    font-size: 0;
    width: 22px;
    height: 22px;
    border: 2px solid white;
    border-radius:  3px;
    position: relative;
  }

  .menu-btn {
    display: block;
    position: absolute;
    background-color: white;
    font-size: 0;
    height: 2px;
    width: 18px;
    top: 10px;
    left: 2px;
    right: 2px;
    transition: background 0s 0.3s;
    -o-transition: background 0s 0.3s;
    -moz-transition: background 0s 0.3s;
    -webkit-transition: background 0s 0.3s;
  }

  .menu-btn::before,
  .menu-btn::after {
    position: absolute;
    display: block;
    height: 2px;
    width: 100%;
    background-color: white;
    content: '';
    transition-duration: 0.3s, 0.3s;  
    -o-transition-duration: 0.3s, 0.3s;  
    -moz-transition-duration: 0.3s, 0.3s;  
    -webkit-transition-duration: 0.3s, 0.3s;  
    transition-delay: 0.3s, 0s;
    -o-transition-delay: 0.3s, 0s;
    -moz-transition-delay: 0.3s, 0s;
    -webkit-transition-delay: 0.3s, 0s;
  }

  span.close {
    background: none;
  }

  span.close::before {
    top: 0;
    rotate: 45deg;
  }

  span.close::after {
    bottom: 0;
    rotate: -45deg;
  }

  .menu-btn::after {
    bottom: -6px;
  }

  .menu-btn::before {
    top: -6px;
  }

  ul{
    width: 150px;
    padding: 0;
  }

  ul>a {
    width: 100%;
    text-align: center;
  }

  label {
    text-align: center;
  }

  .cathead {
    flex-direction: column;
    -ms-flex-direction: column;
  }

  .list-elements {
    flex-direction: column;
    -ms-flex-direction: column;
  }

  .social {
    width: fit-content;
    margin-right: 15px;
  }

  .social:last-child {
    margin: 0;
  }
}

